<template>
    <transition name="fade" appear>
      <ModalView :showModal="showModal" :상자깡="상자깡" :누른상자="누른상자"
      @closeModal="closeModal" />
    </transition>
  <div class="main" ref="scrollWrap">
    <section class="scroll-section" id="scroll-section-0" ref="scrollSection0" :style="`background-image: url(${이미지})`">
      <div class="hero">
        <div class="title">
          Leag of Legends
          <p>10th ANNIVERSARY</p>
          </div>
        <div></div>
        <div class="hero-message a">
          <div class="year">THANK YOU SUMMONERS!</div>
          <div class="greeting">리그오브레전드와 10년간 함께 해주신 소환사 여러분, 감사합니다!</div>
          <div class="greeting">여러 이벤트내용을 확인하고 즐겨보세요!</div>
        </div>
        <button class="hero-bottom-link" @click="goto('evt-tier')">이벤트 대상자인지 확인하러가기</button>
      </div>
    </section>
    <section class="evt">
      <div class="evt-title">특별 출석 이벤트</div>
      <p class="evt-sub">리그오브레전드 10주년을 기념하여 게임에서 진행되는 다양한 이벤트에 참여하고 보상을 받아가세요</p>
      <!-- 일일 출석 이벤트 -->
      <div class="evt-list">
        <div class="evt-list-title">
          게임 일일 출석 보상
        </div>
        <ul class="item-list">
          <li class="item" v-for="(item,i) in resultListLimited" :key="item">
            <div>
              <div class="item-imgs">
                <p class="day">Day{{i+1}}</p>
                <img class="item-img" :src="`http://ddragon.leagueoflegends.com/cdn/10.16.1/img/item/${item.image.full}`">
                <p class="name">{{ item.name }}</p>
              </div>
            </div>
          </li>
        </ul>
      </div>

      <!-- 명품상자 이벤트 -->
      <div class="evt-title">10주년 명품상자 이벤트</div>
      <p class="evt-sub">닉네임을 검색하고 대상자인지 확인해보세요!</p>
      <div class="evt-emote">
        <img class="evt-pengu" v-if="penguIs" v-bind:src="penguFeel?pengu:penguHeart">
      </div>
      <div class="evt-content">
        <div class="evt-form">
          <div class="evt-input-box">
            <input class="evt-input" type="text" @change="input" placeholder="소환사 닉네임 입력" />
          </div>
          <button class="evt-btn" @click="click(), fetchData()">검색</button>
        </div>

        <!-- 소환사 검색 -->
        <div class="evt-tier" ref="evt-tier">
          <div v-if="isVal">
            <div>{{playerIcon}}</div>
            <p>{{playerName}}소환사님의 레벨은 {{playerLevel}}!</p>
            <p>티어는 {{playerTier}} 입니다</p>
            <p v-if="penguFeel">아쉽게도 이벤트 대상자가 아닙니다!</p>
            <p v-else>축하합니다! 이벤트 대상자입니다</p>
          </div>

          <!-- 검색결과없음 -->
          <div v-if="isNan">
            <img :src="require('@/assets/imgs/sad.png')">
            <div class="error-text">{{errorText}}</div>
          </div>
        </div>
        <!-- 티어가 골드시 카드 보여줌 -->
        <div v-if="!penguFeel">
          <transition
            appear
            @before-enter="beforeEnter"
            @enter="enter"
            @afterEnter="afterEnter">
              <div class="tier-cards">
                <div v-for="(card, i) in cards" :key="card.id" class="card" @click="showModal = true, 누른상자 = i"></div>
              </div>
          </transition>
        </div>
      </div>
    </section>
    <section>
      <div class="evt-title">리그오브레전드의 10주년을 축하해주세요</div>
      <p class="evt-sub">응원의 메세지를 남겨주세요!</p>
      <EventComment />
    </section>

    <router-view></router-view>
    <router-link to="/JH">링크</router-link>
  </div>
</template>

<script>
  // import { onMounted, ref} from 'vue'
  import axios from 'axios'
  import { gsap } from "gsap"
  import { ScrollTrigger, ScrollToPlugin } from "gsap/all"
  gsap.registerPlugin(ScrollTrigger, ScrollToPlugin)
  import ModalView from './components/ModalView.vue'
  import EventComment from './components/EventComment.vue'
  import item from './item.json'
  import 상자깡 from './box.js'
  

  export default {
    name: 'App',
    data() {
      return {
        /* main */
        showModal: false,
        isVal : false,
        isNan : false,
        errorText: "존재하지않는 소환사입니다",
        이미지: "https://universe.leagueoflegends.com/images/championsBackground.jpg",
        아이템이미지 : "http://ddragon.leagueoflegends.com/cdn/10.16.1/img/item/",
        pengu : require('@/assets/imgs/pengu.png'),
        penguHeart : require('@/assets/imgs/heart.png'),
        penguIs: true,
        penguFeel : true,
        아이템들: item,
        상자깡: 상자깡,
        누른상자: 0,
        itemLimit: 7,
        /* axios */
        summonInfo: [],
        playerID: '',
        playerName: '',
        playerLevel: '',
        playerTier: '',
        playerIcon:'',
        itemsImg:'',
        unrankMessage: "언랭크",
        apiKey : 'RGAPI-637c55dc-34dc-4568-af81-06d714d1dc42',
        text: '', //출력될 값
        inputText:'', //input값
        cards: [
          { id: 1298 },
          { id: 8748 },
          { id: 4919 },
        ],
      }
    },

    computed: {
      resultList(){
        return Object
        .entries(this.아이템들.data)
        .map(([id, product]) => {
        return{id, ...product}
       })
      },
      //5개만 출력
      resultListLimited(){
        return this.resultList.slice(0, this.itemLimit)
      }

    },
    setup(){
      const beforeEnter = (el) => {
        console.log('before enter - set initial state');
        el.style.transform = 'translateY(-120px)'
        el.style.opacity = 0
      }
      const enter = (el, done) => {
        console.log('starting to enter - make transition');

        //애니메이트할 대상, 오브젝트
        gsap.to(el, {
          duration: 2,
          y: 0,
          opacity: 1,
          ease: 'bounce.out',
          onComplete: done
        });
      }
      const afterEnter = () => {
        console.log('after enter');
      }

      return {
        beforeEnter,
        enter,
        afterEnter
      }
    },

    methods: {
        input(e){
          return this.inputText = e.target.value
          //input에 입력된 값 inputText로 넣어주기
        },
        click(){
          return this.text = this.inputText
          //출력됡밧인 text에 input값을 넣어준 inputText를 넣어주기
        },
        closeModal(){
          this.showModal = false;
        },
        goto(refName) {
          const element = this.$refs[refName];
          const top = element.offsetTop;

          console.log(element);

           window.scrollTo({
            top: top,
            left: 0,
            behavior: 'smooth'
          });
        },
        fetchData: function () {
          axios.get(`https://kr.api.riotgames.com/lol/summoner/v4/summoners/by-name/${this.text}?api_key=${this.apiKey}`)
            .then((res1) => {
              this.summonInfo.push(res1.data);
              this.playerID = (res1.data.id);
              this.playerName = (res1.data.name);
              this.playerLevel = (res1.data.summonerLevel);
              this.playerIcon = (res1.data.profileIconId);
              this.isVal = true;
              this.isNan = false;
              this.penguIs = true;

              console.log(res1.data);

              axios.get(`https://kr.api.riotgames.com/lol/league/v4/entries/by-summoner/${this.playerID}?api_key=${this.apiKey}`)
                .then((res2) => {

                  if (res2.data[0] === undefined) {
                    this.playerTier = this.unrankMessage;
                    this.penguFeel = true;
                  } else {
                    this.playerTier = (res2.data[0].tier);
                    if (this.playerTier == 'GOLD') {
                      this.penguFeel = false;
                      console.log(this.res2.data[0].tier);
                    } else {
                      this.penguFeel = true;
                    }
                  }

                }).catch(error => {
                  console.log("에러입니다" + error);
                });

            }).catch(error => {
              if (error.status == undefined) {
                this.isNan = true;
                this.isVal = false;
                this.penguIs = false;
              }
            });
        },
        isValdata() {
          this.isVal == true;
        },
      },
      mounted() {
        const scrollTrigger = {
          trigger: '.main',
          start: '+=400',
          end: '+=700',
          scrub: true,
          pin: true,
          // markers: true,
        };

        gsap.timeline({
            scrollTrigger
          })
          .from('.hero-message', {
            y: 200,
            opacity: 0
          }).from('.hero-bottom-link', {
            y: 350,
            opacity: 0
          });

      },
      components: {
        ModalView : ModalView,
        EventComment: EventComment
      },
  }
</script>

<style lang="scss">
  body {
    margin: 0;
  }
</style>