<template>
  <div class="comment">
    <div class="commentWrap" v-if="comments.length">
      <ul class="comment-list">
        <li class="comment-list-item" v-for="comments in comments" :key="comments.id">
          {{comments.text}}
        </li>
      </ul>
    </div>
    <div class="userComment">
      <input type="text"
      ref="search"
      class="comment-input"
      v-model="newComments"
      @keypress.enter="addComment"
      placeholder="리그오브레전드 10주년을 축하해주세요!"
      />
    </div>
  </div>
</template>

<script>
  import {ref} from 'vue'

  export default {
    setup(){
      const comments = ref([
        {text: '축하해요 라이엇😍!', id: 1}
      ])

      const newComments = ref('');

      const addComment = () =>{
        const id = Math.random()

        comments.value = [{text: newComments.value, id}, ...comments.value]
        newComments.value = ''
      }

      return{comments, addComment, newComments}
    },
    methods: {
       setFocus()
        {
          this.$refs.search.focus();
        }
    },
    mounted(){
      window.addEventListener('DOMContentLoaded', this.setFocus);
    }
  }
</script>